<!-- 创建用户 -->

<template>
    <div class="login">
        <div class="box">
            <h1 style="margin: auto;">注册</h1><br>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="昵称" prop="name">
                    <el-input v-model="ruleForm.name"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="ruleForm.password"></el-input>
                </el-form-item>
                <el-form-item label="生日" prop="birthday">
                    <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.birthday"
                        style="width: 100%;"></el-date-picker>
                </el-form-item>
                <el-form-item label="电话" prop="tell">
                    <el-input v-model="ruleForm.tell"></el-input>
                </el-form-item>
                <el-form-item label="姓名" prop="realname">
                    <el-input v-model="ruleForm.realname"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input v-model="ruleForm.email"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
                    <el-button @click="resetForm('ruleForm')">重置</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
const options = {
    data() {
        return {
            ruleForm: {
                name: '',
                password: '',
                day: new Date(),
                banday: new Date(),
                birthday: '',
                admin: 0,
                tell: '',
                realname: '',
                email: ''
            },
            rules: {
                name: [
                    { required: true, message: '请输入您的昵称', trigger: 'blur' },
                    { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '请输入您的密码', trigger: 'blur' },
                    { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
                ],
                birthday: [
                    { required: true, message: '请输入您的生日', trigger: 'blur' },
                    { type: 'date', trigger: 'blur' }
                ],
                tell: [
                    { required: true, message: '电话不能为空', trigger: 'blur' },
                    { pattern: /^1[0-9]\d{9,11}$/, message: '电话必须为数字值且是11-12位', trigger: 'blur' }
                ],
                realname: [
                    { required: true, message: '请输入您的真名', trigger: 'blur' },
                    { pattern: /^[\u4E00-\u9FA5\uf900-\ufa2d·s]{2,10}$/, message: '请输入中文且长度2-10位', trigger: 'blur' }
                ],
                email: [
                    { required: true, message: '请输入您的邮箱', trigger: 'blur' },
                    { trigger: 'blur' }
                ]

            }
        };
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    //发axios请求
                    axios.request({
                        baseURL: "http://localhost:8090/",
                        url: "/user/add",
                        data: this.ruleForm,
                        method: 'post',
                        headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
                    })
                        .then(res => {
                            if (res.data) {
                                alert("创建成功，即将跳转");
                                this.push('/UserMenu')
                            } else {
                                alert("用户名重复，请再次输入");
                                this.resetForm('ruleForm')
                            }

                        });

                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        push(url) {
            this.$router.push(url);
        }

    }
}
export default options;

</script>


<style scoped>
.login {
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 0;
    text-decoration: none;
    height: 100vh;
    background-size: cover;
    width: 550px;
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to right bottom,
            rgba(255, 255, 255, .7),
            rgba(255, 255, 255, .5),
            rgba(255, 255, 255, .4));
    /* 使背景模糊化 */
    backdrop-filter: blur(10px);
    box-shadow: 0 0 20px #a29bfe;
    border-radius: 15px;
}

.table {
    font: 900 40px '';
    text-align: center;
    letter-spacing: 5px;
    color: #3d3d3d;
}

.box {
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.box input {
    width: 400px;
    height: 100%;
    margin-bottom: 20px;
    outline: none;
    border: 0;
    padding: 10px;
    background-color: transparent;
    border-bottom: 3px solid rgb(150, 150, 240);
    font: 900 16px '';
}
</style>